import React from 'react';
import './App.css';
import AboutImage from './components/AboutImage';
import MyLinks from './components/MyLinks';
import AboutPage from './components/AboutPage';
import ContactPage from './components/ContactPage';
import 'bootstrap/dist/css/bootstrap.min.css';
import ContactImage from './components/ContactImage';
import Footer from './components/Footer';
import { Navbar, Nav, Button } from 'react-bootstrap';
import logo from './components/TransparentLogo.png';
import HomePage from './components/HomePage';
import MetaTags from 'react-meta-tags';
import ndlogo from './ndlogo.png';

class App extends React.Component {

linkToShop() {
    window.location.href='https://ninthdegreeuk.bigcartel.com'
}

scrollToAbout(){
    window.location.href = "#about"
}

scrollToContact(){
  window.location.href = "#contact"
}

scrollToTop(){
    window.scrollTo(0, 0);
}

  render(){
    return (
      <div className="App" style={{float:'left'}}>
          <MetaTags>
            <title>Ninth Degree</title>
            <meta name="description" content="The home of Ninth Degree- an exciting new scottish rock band" />
            <meta property="og:title" content="Ninth Degree" />
            <meta property="og:image" content={ndlogo} />
            <meta name="twitter:card" content="summary_large_image"/>
          </MetaTags>
        <header className="App-header">

          <div>
            <Navbar collapseOnSelect fixed="top" expand="lg" bg="dark" variant="dark" style={{padding: '2vh', backgroundColor:'#434d56' }}>
              <Navbar.Brand href="#home"  onClick={this.scrollToTop} ><img src= {logo} style={{height: '10vh' }} ></img></Navbar.Brand>
              <Navbar.Toggle aria-controls="responsive-navbar-nav" />
              <Navbar.Collapse id="responsive-navbar-nav">
              <Nav className="mr-auto">
                  <Nav.Link eventKey="1"> <Button variant="dark" onClick={this.scrollToAbout} style={{fontSize: 'calc(10px + 2vmin)'}}>About</Button></Nav.Link>
                  <Nav.Link eventKey="2"> <Button variant="dark" onClick={this.linkToShop} style={{fontSize: 'calc(10px + 2vmin)'}}>Shop</Button></Nav.Link>
                  <Nav.Link eventKey="3"> <Button variant="dark" onClick={this.scrollToContact} style={{fontSize: 'calc(10px + 2vmin)'}}>Contact</Button></Nav.Link>
              </Nav>
              </Navbar.Collapse>
            </Navbar>
          </div>

          <div style={{paddingTop:'14.7vh'}}>
            <MyLinks/>
            <HomePage/>

            <div id='about'>
            </div>
            <AboutImage/>
            <AboutPage/>
          
            <div id='contact'>
            <ContactImage/>
            <ContactPage/>

            </div>
          </div>

          <footer style={{backgroundColor:'#434d56', width:'100%'}}>
            <Footer/>
          </footer>
        </header>

      </div>
    );
  }
}

export default App;
